
* {
  margin: 0;
  outline: 0;
  padding: 0;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}
html,
body,
textarea {
  font-family: "Open Sans", sans-serif;
  font-size: 12px;
}
.General__Container {
  margin: 0 auto;
  max-width: 720px;
  padding: 0 3.13vw;
  width: 100%;
}
@font-face {
  font-family: "Open Sans";
  font-weight: normal;
  font-style: normal;
  src: url("assets/fonts/opensans-regular-webfont.woff2") format("woff2"), url("assets/fonts/opensans-regular-webfont.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  font-style: normal;
  src: url("assets/fonts/opensans-bold-webfont.woff2") format("woff2"), url("assets/fonts/opensans-bold-webfont.woff") format("woff");
}
.Alert,
.Alert--default {
  font-size: 14px;
  position: fixed;
  top: 0;
  left: 0;
  padding: 20px;
  width: 100vw;
}
.Alert--default,
.Alert--default--default {
  background-color: rgba(51,51,51,0.95);
  color: #fff;
}
.NoticeModal {
  position: absolute;
  width: 100%;
  font-size: 1rem;
}
.NoticeModal__Mask {
  background-color: rgba(0,0,0,0.76);
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  z-index: 8;
}
.NoticeModal__Box {
  background-color: #fff;
  position: fixed;
  margin: -57px 0 0 -155px;
  width: 310px;
  padding: 20px 15px;
  left: 50%;
  top: 50%;
  text-align: center;
  z-index: 9;
}
.NoticeModal__Text {
  margin-bottom: 12px;
  line-height: 1.5rem;
}
.NoticeModal__Text a {
  color: #333;
  text-decoration: underline;
}
.NoticeModal__Btn,
.NoticeModal__Btn--secondary {
  background-color: #3263a3;
  border: none;
  color: #fff;
  cursor: pointer;
  padding: 5px 7px;
}
.NoticeModal__Btn--secondary,
.NoticeModal__Btn--secondary--secondary {
  background-color: #999;
}


